body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

nav {
  padding: 0 50px;
}

.content {
  min-height: calc(100vh - 135px);
  padding: 20px 0;
}

.goods {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.card {
  display: flex;
  flex-direction: column;
}
.card-content {
  flex-grow: 1;
}

.cart {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  padding: 1rem;
  cursor: pointer;
  z-index: 3;
}

@media (min-width: 767px) {
  .cart {
    top: 5rem;
    bottom:unset;
  }
}
.cart-quantity {

}

.basket-list {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  width: 50rem;
  max-width: 95%;
  animation: show 500ms ease-in-out;

}

.basket-close {
  cursor: pointer;
  position: absolute;
  top: 0.5rem;
  right: 1.3rem;
  color: white;
}
.basket-delete {
  cursor: pointer;
}

@keyframes show {
  from {
    top: 70%;
    opacity: 0;
  }
  to {
    top: 50%;
    opacity: 1;
  }
}
.basket-quantity {
  cursor: pointer;
  vertical-align: middle;
  color: #03a9f4;
}
.btn:hover {
  background-color: #01579b !important;
}

.btn-small {
  position: relative;
  bottom: 5px;
}

#toast-container {
  top: 3rem !important;
  animation: fade-in 250ms ease-in-out
}

@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateY(2rem);
  }
   to {
    opacity: 1;
    transform: translateY(0rem);
   }
}